import React, { FC } from "react";

import {
	BottomControlsWrapper,
	BreadcrumbsWrapper,
	LeftBottomControls,
	LeftContent,
	LeftContentWrapper,
	MainPageHeaderContent,
	MainTitleContent,
	PageHeaderWrapper,
	RightBottomControls,
	RightContentWrapper,
	SubTitleContent,
} from "./PageHeader.styles";
import { PageHeaderProps } from "./PageHeader.types";

const PageHeader: FC<PageHeaderProps> = ({
	breadcrumbs,
	dataQA,
	leftContentAction,
	titleContent,
	subtitleContent,
	rightContent,
	leftBottomControls,
	rightBottomControls,
	...props
}) => {
	const breadcrumbsComponent = breadcrumbs && (
		<BreadcrumbsWrapper
			aria-label="Page header Breadcrumbs"
			data-qa={`${dataQA}-breadcrumbs`}
		>
			{breadcrumbs}
		</BreadcrumbsWrapper>
	);
	return (
		<PageHeaderWrapper data-qa={dataQA} {...props}>
			{breadcrumbsComponent}
			<MainPageHeaderContent>
				<LeftContentWrapper>
					{leftContentAction}
					<LeftContent>
						<MainTitleContent>
							{titleContent}
						</MainTitleContent>
						<SubTitleContent>
							{subtitleContent}
						</SubTitleContent>
					</LeftContent>
				</LeftContentWrapper>
				<RightContentWrapper>
					{rightContent}
				</RightContentWrapper>
			</MainPageHeaderContent>
			{(leftBottomControls || rightBottomControls) && (
				<BottomControlsWrapper>
					<LeftBottomControls>
						{leftBottomControls}
					</LeftBottomControls>
					<RightBottomControls>
						{rightBottomControls}
					</RightBottomControls>
				</BottomControlsWrapper>
			)}
		</PageHeaderWrapper>
	);
};

PageHeader.displayName = "PageHeader";
export default PageHeader;
